
//CSS

import '../assets/css/pages/edit-profile.css';

//Icons
import IcCamera from "../assets/images/icons/ic-camera-white.svg";

//Images
import profileImg from '../assets/images/backgrounds/dummy-user.png';


function editProfile() {
    return (
        <div className="main-content">
            <section className="edit-profile">
                <div className="container">
                    <div className="section-title varient-two">
                        <h4>My Profile</h4>
                    </div>
                    <div className="light-blue-bg varient-two">
                        <div className='text-center'>
                            <div className='user-profile-img-wrap'>
                                <div className='user-profile-img'>
                                    <img src={profileImg} className="cover-fit" alt="" />
                                </div>
                                <button className='upload-profileImg-btn'>
                                    <img src={IcCamera} alt="" />
                                    <input type="file" />
                                </button>
                            </div>

                        </div>
                        <div className="row">
                            <div className="col-lg-6 col-md-12">
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label htmlFor="first_name" className="form-label">First Name</label>
                                            <input type="text" className="form-control" id="first_name" placeholder="Enter Your First Name" />
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label htmlFor="last_name" className="form-label">Last Name</label>
                                            <input type="text" className="form-control" id="last_name" placeholder="Enter Your Last Name" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-6 col-md-12">
                                <div className="form-group">
                                    <label htmlFor="first_name" className="form-label">Designation</label>
                                    <input type="text" className="form-control" id="first_name" placeholder="Enter Your Designation" />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group">
                                    <label htmlFor="email" className="form-label">Email</label>
                                    <input type="email" className="form-control" id="email" placeholder="Enter your email" />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group">
                                    <label htmlFor="phone_number" className="form-label">Phone Number</label>
                                    <input type="text" className="form-control" id="phone_number" placeholder="Enter Your Phone Number" />
                                </div>
                            </div>
                        </div>
                        <h4 className="form-block-title">Experience</h4>
                        <div className="row">
                            <div className="col-lg-6">
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label htmlFor="experience_year" className="form-label">Years</label>
                                            <input type="text" className="form-control" id="experience_year" placeholder="Years" />
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label htmlFor="experience_month" className="form-label">Month</label>
                                            <input type="text" className="form-control" id="experience_month" placeholder="Month" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-6">
                                <div className="form-group">
                                    <label className="form-label">Certificate</label>
                                    <label htmlFor="upload_certificate_input" className='upload_certificate form-control'>
                                        Certificate
                                        <input type="file" id="upload_certificate_input" />
                                    </label>  
                                </div>
                            </div>
                        </div>
                        <h4 className="form-block-title">Address</h4>
                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group">
                                    <label htmlFor="house_number" className="form-label">House Number</label>
                                    <input type="text" className="form-control" id="house_number" placeholder="Years" />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group">
                                    <label htmlFor="street_name" className="form-label">Street Name</label>
                                    <input type="text" className="form-control" id="street_name" placeholder="Month" />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group">
                                    <label htmlFor="city_name" className="form-label">City Name</label>
                                    <input type="text" className="form-control" id="city_name" placeholder="Month" />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group">
                                    <label htmlFor="state_name" className="form-label">State Name</label>
                                    <input type="text" className="form-control" id="state_name" placeholder="Month" />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group">
                                    <label htmlFor="country_name" className="form-label">Country Name</label>
                                    <input type="text" className="form-control" id="country_name" placeholder="Month" />
                                </div>
                            </div>
                        </div>
                        <div className='row'>
                            <div className='col-lg-4'>
                                <button className='button button-blue w-100'>Save</button>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default editProfile;